.all{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 10px;
    grid-auto-rows: minmax(50px, auto);
    
    background-color: #ffccd5;
}

.home{
    grid-column:1/12 ;
    grid-row:1;
}

.card{
    
    grid-column: 1/2;
    grid-row: 3;

    
  }
    

.searchBar{
    grid-template-columns: 100px 1fr;
    height: 20px;
    width: 150px;
    grid-row: 2 ;
}